@charset "UTF-8";
@import "common.scss";

.listCon{
    position: relative;
    z-index: 1;
    padding: 240px 0 350px;
    .subNav{
        position: relative;
        z-index: 1;
        max-width: 1200px;
        margin: 0 auto 170px;
        >ul{
            >li{
                position: relative;
                float: left;
                margin-right: 30px;
                .tit{
                    position: relative;
                    padding-right: 60px;
                    font: 48px 'crimson';
                    line-height: 40px;
                    font-size: 62px;
                    p{
                        color: #2c3d45;
                        font: 48px 'crimson';
                        line-height: 40px;
                        font-size: 62px;
                        transform: translateY(100px);
                        transition: all 1.6s cubic-bezier(0.475, 0.425, 0, 0.995);
                    }
                    p::after{
                        content: "";
                        position: absolute;
                        left: 0; top: -10px;
                        border: 6px solid #d6ad41;
                        border-radius: 6px;
                        opacity: 0;
                        transition: .1s;
                        transition-delay: 1.5s;
                    }
                    span{
                        position: absolute;
                        left: 0; bottom: -10px;
                        z-index: -1;
                        display: inline block;
                        width: 0px;
                        border-bottom: 10px solid #d6ad41;
                        transition: all 1s cubic-bezier(0.475, 0.425, 0, 0.995);
                        transition-delay: .3s;
                    }
                }
                .tit.show{
                    p{transform: translateY(0px);}
                    p::after{opacity: 1;}
                    span{width: 230px;}
                }
            }
            .history{
                position: relative;
                float: right;
                padding-top: 20px;
                margin-right: 0;
                font: 18px 'crimson';
                a::before{
                    opacity: 0;
                    content: "";
                    position: absolute;
                    left: 50%; bottom: 0;
                    width: 0; height: 0;
                    background: #000;
                    transition: left .1s cubic-bezier(0, 0, 1, 1);
                }
                a{
                    position: relative;
                    display: block;
                    padding: 0 30px 2px 0;
                    margin: 0 -30px -2px 0;
                    color: #000;
                }
                a::after{
                    opacity: 0;
                    content: "";
                    position: absolute;
                    left: 0; bottom: 0;
                    width: 50%; height: 1px;
                    background: #000;
                    transition: all .1s cubic-bezier(0, 0, 1, 1);
                }
            }
        }
        >ul::after{
            content: "";
            display: block;
            clear: both;
        }
    }
    .proList{
        ul{
            position: relative;
            max-width: 1200px;
            margin: 0 auto;
            display: flex;
            flex-wrap: wrap;
            li{
                position: relative;
                width: 25%;
                transition: transform .3s cubic-bezier(0.165, 0.84, 0.44, 1), -webkit-transform .3s cubic-bezier(0.165, 0.84, 0.44, 1);
                a{
                    position: absolute;
                    left: 0; top: 0;
                    overflow: hidden;
                    display: block;
                    img{
                        width: 100%;
                        transform:scale(1);
                        transition: all .3s cubic-bezier(0, 0, 1, 1);
                    }
                    .deta{
                        position: absolute;
                        left: 50%; top: 50%;
                        z-index: 2;
                        width: 100%;
                        text-align: center;
                        opacity: 0;
                        transform: translate(-50%, -50%);
                        transition: all .3s cubic-bezier(0, 0, 1, 1);
                        .txt{
                            color: #fff;
                            font: 13px 'NanumSquare';
                            font-weight: 300;
                            transform: translateY(20px);
                            transition: all .3s cubic-bezier(0, 0, 1, 1);
                            strong{
                                display: inline-block;
                                margin-top: 10px;
                                border-bottom: 3px solid #d6ad41;
                                font-size: 20px;
                                line-height: 1em;
                            }
                        }
                        .link{
                            margin-top: 50px;
                            font-size: 11px;
                            color: #fff;
                            opacity: .7;
                            transform: translateY(20px);
                            transition: all .3s cubic-bezier(0, 0, 1, 1);
                        }
                    }
                }
                a::after{
                    content: "";
                    position: absolute;
                    left: 0; top: 0;
                    right: 0; bottom: 0;
                    z-index: 1;
                    background-color: rgba($color: #2c3d45, $alpha: .6);
                    opacity: 0;
                }
            }
            li:hover{
                img{transform:scale(1.1)}
                a::after{opacity: 1;}
                .deta{opacity: 1;}
            }
        }
        ul::after{
            content: "";
            display: block;
            clear: both;
        }
    }
}