@charset "UTF-8";
@import "common.scss";

.projectDetail{
    // background-image: url(../../img/slow/sub/bg.png);
    background-position-y: 0px;

    overflow-x: cover;
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-attachment: fixed;
    transition: all 1s cubic-bezier(0, 0.83, 0.44, 1);
    .proAll{
        position: relative;
        width: 100%;
        min-width: 1024px;
        .container{
            position: relative;
            padding: 0;
            margin-bottom: 0;
            background: none;
            z-index: 1;
            .projectHead{
                position: relative;
                z-index: 1;
                max-width: 1200px;
                height: 978px;
                margin: 0 auto;
                .close{
                    opacity: 1;
                    position: absolute;
                    right: 50px; top: 50px;
                    z-index: 1;
                    animation: btnClose .3s linear 1s 1 normal both;
                    @keyframes btnClose {
                        0%{opacity: 0; transform: rotate(0);}
                        1%{opacity: 1;}
                        100%{opacity: 1; transform: rotate(90deg);}
                    }
                    a::before{
                        content: "";
                        position: absolute;
                        left: -8px; top: 50%;
                        width: 54px; height: 2px;
                        background: #fff;
                        transform: rotate(45deg);
                        transform-origin: 50%;
                        cursor: pointer;
                    }
                    a{
                        display: block;
                        overflow: hidden;
                        width: 38px; height: 38px;
                        text-indent: 100%;
                        white-space: nowrap;
                        cursor: pointer;
                        transform: rotate(0deg);
                        transition: .3s;
                    }
                    a:hover{
                        transform: rotate(-90deg);
                        transition: .3s;
                    }
                    a::after{
                        content: "";
                        position: absolute;
                        left: -8px; top: 50%;
                        width: 54px; height: 2px;
                        background: #fff;
                        transform: rotate(-45deg);
                        transform-origin: 50%;
                        cursor: pointer;
                    }
                }
                h2{
                    padding-top: 90px;
                    text-align: center;
                    font: 48px 'NanumSquare';
                    font-weight: 300;
                    line-height: 64px;
                    color: #fff;
                    animation: focusShow .2s linear .3s 1 normal both;
                    @keyframes focusShow {
                        0%{opacity: 0; transform: translateY(30px);}
                        100%{opacity: 1; transform: translateX(0);}
                    }
                }
                .nav{
                    margin-top: 25px;
                    text-align: center;
                    animation: focusShow .2s linear .5s 1 normal both;
                    @keyframes focusShow {
                        0%{opacity: 0; }
                        100%{opacity: 1; transform: translateX(0);}
                    }
                    li{
                        display: inline-block;
                        margin: 0 23px;
                        font-size: 9px;
                        opacity: .6;
                        cursor: pointer;
                        a::before{
                            content: "";
                            position: absolute;
                            top: 0;
                            height: 12px;
                            border: 2px solid #fff;
                            border-bottom: 0;
                            opacity: .6;
                        }
                        a{
                            position: relative;
                            display: inline-block;
                            padding: 18px 0 0 4px;
                            color: #fff;
                            letter-spacing: 4px;
                        }
                    }
                    li.on{
                        opacity: 1;
                        a::before{
                            content: "";
                            position: absolute;
                            top: 0;
                            height: 12px;
                            border: 2.5px solid #fff;
                            border-bottom: 0;
                            opacity: 1;
                        }
                    }
                    li:hover{
                        opacity: 1;
                        a::before{
                            content: "";
                            position: absolute;
                            top: 0;
                            height: 12px;
                            border: 2.5px solid #fff;
                            border-bottom: 0;
                            opacity: 1;
                        }
                    }
                }
                .nav.show{
                    li.pc{
                        a::before{left: 10px; right: 10px;}
                    }
                    li.mobile{
                        a::before{left: 23px; right: 23px;}
                    }
                    li.tablet{
                        a::before{left: 17px; right: 17px;}
                    }
                }
                .visual{
                    position: relative;
                    height: 700px;
                    p{
                        position: absolute;
                        left: 50%; bottom: -10px;
                        opacity: 0;
                        transform: translateX(-40%);
                        transition: all .3s cubic-bezier(0.165, 0.84, 0.44, 1);
                        transition-delay: .1s;
                    }
                    p.on{
                        opacity: 1;
                        transform: translateX(-50%);
                        animation: xShow .2s linear .3s 1 normal both;
                        @keyframes xShow {
                            0%{opacity: 0; transform: translateX(0%);}
                            100%{opacity: 1; transform: translateX(-50);}
                        }
                    }
                    p.pc{
                        width: 943px; height: 645px;
                        padding: 6px 0 0 6px;
                        background: url(../../img/detail/frame_pc.png) no-repeat 50% 0;
                        img{width:99.5%; height: 83%;}
                    }
                    p.mobile{
                        width: 318px; height: 551px;
                        padding: 47px 0 0 39px;
                        background: url(../../img/detail/frame_mobile.png) no-repeat 50% 0;
                        img{width:87.1%; height: 86%;}
                    }
                    p.tablet{
                        width: 886px; height: 516px;
                        padding: 22px 0 0 52px;
                        background: url(../../img/detail/frame_tablet.png) no-repeat 50% 0;
                        img{width:94%; height: 92%;}
                    }
                }
                .site{
                    position: relative;
                    height: 78px;
                    margin-top: -78px;
                    animation: focusShow .2s linear 1s 1 normal both;
                    @keyframes focusShow {
                        0%{opacity: 0; transform: translateY(30px);}
                        100%{opacity: 1; transform: translateX(0);}
                    }
                    .btn{
                        position: absolute;
                        right: 0; bottom: 0;
                        a::before{
                            content: "";
                            position: absolute;
                            right: 0; bottom: 10px;
                            width: 65px; height: 2px;
                            background: #d6ad41;
                            transition: all .3s cubic-bezier(0, 0, 1, 1);
                        }
                        a{
                            position: relative;
                            display: block;
                            height: 100%;
                            padding-right: 80px;
                            margin-right: -70px;
                            font-size: 10px;
                            color: #777;
                            transition: all .3s cubic-bezier(0.165, 0.84, 0.44, 1);
                            strong{
                                display: inline-block;
                                padding-top: 30px;
                                font-size: 20px;
                                line-height: 15px;
                                font-weight: 700;
                                color: #000;
                                opacity: .5;
                                transition: .5s;
                                transition: all .3s cubic-bezier(0.165, 0.84, 0.44, 1);
                            }
                        }
                        a::after{
                            content: "";
                            position: absolute;
                            right: 0; bottom: 16px;
                            width: 16px; height: 2px;
                            background: #d6ad41;
                            transform: rotate(45deg);
                        }
                        a:hover::before{
                            content: "";
                            position: absolute;
                            right: 0; bottom: 10px;
                            width: 160px; height: 2px;
                            background: #d6ad41;
                            transition: all .3s cubic-bezier(0, 0, 1, 1);
                        }
                    }
                }
            }
            .feature{
                padding-top: 245px;
                margin-top: -130px;
