                        }
                        .txt.show{opacity: 1;}
                        .txt.show::after{top: 100%;}
                        .txt::after{
                            content: "";
                            position: absolute;
                            left: 0; right: 0;
                            bottom: 0; top: -30px;
                            background: linear-gradient(to bottom, transparent 0, #fff 30px, #fff 0);
                            transition: all 3s cubic-bezier(0.165, 0.84, 0.44, 1);
                        }
