                        line-height: 30px;
                        color: #4d4d4d;
                        transition: all .3s cubic-bezier(0, 0, 1, 1);
                    }
                }
                li::after{
                    content: "";
                    position: absolute;
                    background: #d6ad41;
                    transition: all .3s cubic-bezier(0, 0, 1, 1);
                }

                .first::before{left: 0;}
                .first:hover::before{background: #d6ad41;}
                .first:hover::after{
                    transform: rotate(-45deg); 
                    left: 0; bottom: 35px;
                    width: 16px; height: 3px;
                }
                .first:hover{
                    a{font-size: 22px; color: #d6ad41;}
                }

                .second::before{right: 0;}
                .second:hover::before{background: #d6ad41;}
                .second:hover::after{
                    transform: rotate(45deg); 
                    width: 16px; height: 3px;
                    right: 0; bottom: 35px;
                }
                .second{
                    float: right;
                    padding-left: 0;
                    padding-right: 220px;
                    text-align: right;
                    a{}
                }
                .second:hover{
                    a{font-size: 22px; color: #d6ad41;}
                }
            }
        }






        .btnTop{
            opacity: 0;
            position: fixed;
            right: 50px; bottom: 50px;
            z-index: 50;
            width: 70px; height: 70px;
            border-radius: 50%;
            background: rgba(255,255,255,0.5);
            a{
                display: block;
                width: 70px; height: 70px;
                border: 1px solid #d6ad41;
                border-radius: 50%;
                text-align: center;
            }
            em::before{
                content: "";
                position: absolute;
                left: 40%; top: 24px;
                width: 20px; height: 3px;
                margin-left: -12px;
                background: #2c3d45;
                transform-origin: 100%;
                transform: rotateZ(-50deg);
                transition: all .3s ease-in-out;
            }
            em{
                position: relative;
                z-index: 2;
                width: 68px; height: 68px;
                overflow: hidden;
                display: block;
                text-indent: 100%;
                white-space: nowrap;
                font: 700 18px/56px 'Montserrat';
                color: #2c3d45;
            }
            em::after{
                content: "";
                position: absolute;
                left: 50%; top: 24px;
                width: 20px; height: 3px;
                background: #2c3d45;
                transform-origin: 0;
                transform: rotateZ(50deg);
                transition: all .3s ease-in-out;
            }
        }
        .btnTop.show{
            opacity: 1;
            transition: opacity .2s ease-in-out;
        }
    }
}