// About.js
.about{
    overflow: auto;
    width: 100%;
    margin-top: 0;
    background: #fff;
    position: relative;
    padding-left: 0%;
    z-index: 30;
    transition: all .8s cubic-bezier(0.165, 0.84, 0.44, 1);
    .nav{
        position: fixed;
        left: 5%; top: 100%;
        margin-top: 0%;
        z-index: 999999999;
        transition: margin .8s cubic-bezier(0.165, 0.84, 0.44, 1);
        ul{
            li::before{
                content: "";
                position: absolute;
                left: 10%; top: 50%;
                width: 0; height: 2px;
                margin-top: -1px;
                background: #000;
                transition: width .3s cubic-bezier(1, 0, 0, 0.7);
            }
            li{
                position: relative;
                margin-bottom: 30px;
                padding-left: 150px;
                transition: margin .5s cubic-bezier(1, 0, 0, 0.7);
                a{
                    font-size: 16px;
                    font-weight: 700;
                    line-height: 1em;
                    color: #050505;
                    opacity: .5;
                    transition: opacity .3s cubic-bezier(1, 0, 0, 0.7);
                }
            }
            li.on::before{
                width: 100px;
            }
            li.on{
                margin-bottom: 500px;
                a{opacity: 1;}
            }

        } 
    }
    
    #intro{z-index: 101;}
    #info{z-index: 102;}
    #etc{z-index: 103;}
    #contact{z-index: 104;}
    .content{
        position: relative;
        // padding: 0 120px;
        width: 100%; height: 100vh;
        position: fixed;
        background: #fff;
        transition:.5s;
        top: 100%;
        &.up{
            top: 0%;
        }

        .introCon{
            position: absolute;
            left: 60%; top: 35%;
            transform: translateX(-100%);
            .inTxt{
                p{
                    overflow: hidden;
                    font: 60px 'crimson';
                    color: #000;
                    margin-top: 20px;
                    span::before{
                        content: "";
                        position: absolute;
                        left: 0; bottom: 10px;
                        z-index: -1;
                        width: 0; height: 8px;
                        background: #d6ad41;
                        transition: all 1s cubic-bezier(0.475, 0.425, 0, 0.995);
                        transition-delay: .3s;
                    }
                    span{
                        position: relative;
                        overflow: hidden;
                        display: inline-block;
                        line-height: 1.2em;
                        vertical-align: top;
                        opacity: 0;
                        white-space: nowrap;
                        transform: translateY(100px);
                        transition: all 1.6s cubic-bezier(0.475, 0.425, 0, 0.995);
                        strong{
                            color: #2c3d45;
                        }
                        strong::after{
                            content: "";
                            position: absolute;
                            left: 280px; top: 0px;
                            border: 1px solid #d6ad41;
                            border-radius: 1px;
                            opacity: 0;
                            transition: .1s;
                            transition-delay: 1.5s;
                            // transform: translateY(100px);
                            // transition: all 1.6s cubic-bezier(0.475, 0.425, 0, 0.995);
                        }
                    }
                }
            }
            .inCon{
                margin-top: 20px;
                color: #444;
                font-size: 14px;
                line-height: 20px;
                transform: translateY(50px);
                transition: all 2s cubic-bezier(0.475, 0.425, 0, 0.995);
            }
        }
        
        .introPho{
            position: absolute;
            right: 4%; top: 0;
            width: 36%; height: 100%;
            transform: translateX(200px);
            transition: all 2s cubic-bezier(0.475, 0.425, 0, 0.995);
            img{width: 100%;}
        }

        .insk{
            float: left;
            position: absolute; 
            left: 25%; top: 25%;
            width: 45%;
            .infoCon{
                display: flex;
                justify-content: space-between;
                margin-bottom: 100px;
                .con{
                    span::before{
                        content: "";
                        position: absolute;
                        left: 0; bottom: 0px;
                        z-index: -1;
                        width: 0; height: 8px;
                        background: #d6ad41;
                        transition: all 1s cubic-bezier(0.475, 0.425, 0, 0.995);
                        transition-delay: .3s;
                    }
                    span{
                        position: relative;
                        overflow: hidden;
                        display: inline-block;
                        vertical-align: top;
                        color: #2c3d45;
                        font-size: 2em;
                        font-weight: bold;
                        line-height: 1em;
                        margin-bottom: 20px;
                        opacity: 0;
                        transform: translateY(30px);
                        transition: all 1.6s cubic-bezier(0.475, 0.425, 0, 0.995);
                    }
                    p{
                        font-size: 16px;
                        margin-bottom: 5px;
                    }
                }
            }
            .skillCon{
                .conWrap{
                    overflow: hidden;
                    position: relative;
                    height: 100vh;
                    max-width: 1440px;
                    margin: 0 auto;
                    transition: .5s;
                    padding-right: 200px;
                    .tit::before{
                        content: "";
                        position: absolute;
                        left: 0; bottom: 0px;
                        z-index: -1;
                        width: 0; height: 8px;
                        background: #d6ad41;
                        transition: all 1s cubic-bezier(0.475, 0.425, 0, 0.995);
                        transition-delay: .3s;
                    }
                    .tit{
                        position: relative;
                        overflow: hidden;
                        display: inline-block;
                        vertical-align: top;
                        color: #2c3d45;
                        font-size: 2em;
                        font-weight: bold;
                        line-height: 1em;
                        opacity: 0;
                        transform: translateY(30px);
                        transition: all 1.6s cubic-bezier(0.475, 0.425, 0, 0.995);
                    }
                    .proWrap{
                        float: right;
                        width: 40%;
                        min-width: 380px;
                        position: relative;
                        // transform: translateY(10px);
                        .proBox{
                            padding-bottom: 100%;
                            .pro{
                                display: block;
                                position: absolute;
                                width: 100%; height: 100%;
                                border-radius: 50%;
                                background: transparent;
                                .txtBox{
                                    position: absolute;
                                    width: 90%; height: 90%;
                                    border-radius: 50%;
                                    top: 50%; left: 50%;
                                    transform: translate(-50%, -50%);
                                    text-align: center;
                                    color: #d6ad41;
                                    z-index: 10;
                                    .txtBoxIn{
                                        position: absolute;
                                        background: #ffffff;
                                        width: 80%; height: 80%;
                                        border-radius: 50%;
                                        top: 50%; left: 50%;
                                        transform: translate(-50%, -50%);
                                        text-align: center;
                                        color: #d6ad41;
                                        z-index: 10;
                                        .txt{
                                            position: absolute;
                                            left: 51%; top: 13%;
                                            transform: translateX(-50%);
                                            font-size: 5em;
                                            font-weight: bold;
                                            line-height: 1.3em;
                                            color: #d6ad41;
                                        }
                                        .skilltxt{
                                            position: absolute;
                                            left: 51%; top: 50%;
                                            transform: translateX(-50%);
                                            line-height: 1.5em;
                                            width: 300px;
                                            color: #d6ad41;
                                            opacity: 0;
                                            &.on{
                                                opacity: 1;
                                            }
                                        }
                                    }
                                }
                                .gageBar{
                                    position: absolute;
                                    display: none;
                                    top: 2%; left: 50%;
                                    width: 6px; height: 48%;
                                    background: #2c3d45;
                                    transform-origin: 0 100%;
                                    z-index: 9;
                                    transform: rotate(0deg);
                                    display: block;
                                    opacity:0;
                                    transition:all 0.1s;
                                    // transform-origin:left bottom; 
                                }
                                .gageBar.on{opacity: 1;}
                            }
                        }
                    }
                    .proWrap::after{
                        display: block;
                        content: "";
                        position: absolute;
                        top: 50%; left: 50%;
                        transform: translate(-50%, -50%);
                        width: 80%; height: 80%;
                        border-radius: 50%;
                        border: 10px solid #d6ad41;
                    }
                    .skList{
                        position: absolute;
                        top: 21%;
                        transform: translateY(-50%);
                        overflow: hidden;
                        li{
                            width: auto;
                            margin-bottom: 20px;
                            cursor: pointer;
                            a{
                                display: inline;
                                width: auto;
                                font-size: 24px;
                                font-weight: bold;
                                line-height: 1em;
                                height: 100%;
                                text-decoration: none;
                                color: #d6ad41;
                                &.on{
                                    transition: .5s;
                                    color: #2c3d45;
                                }
                            }
                        }
                    }
                }
                
            }
        }
        .introPho2{
            float: right;
            position: absolute;
            right: 0%; top: 0;
            width: 36%; height: 100%;
            transform: translateX(200px);
            transition: all 2s cubic-bezier(0.475, 0.425, 0, 0.995);
            img{width: 100%;}
        }

        .wrap{
            position: relative;
            max-width: 1200px;
            margin: 0 auto;
            background: url(../../img/detail/bg_pattern4.png) no-repeat 50% -330px;
            h3.typeA{
                position: absolute;
                left: 100px; top: 290px;
                margin-top: 0;
                margin: 80px auto 35px;
                color: #d6ad41;
                >span::before{
                    content: "";
                    position: absolute;
                    left: 0; bottom: 0;
                    width: 0; height: 3px;
                    background: #d6ad41;
                    transition: all .3s cubic-bezier(0, 0, 1, 1);
                }
                >span{
                    position: relative;
                    display: inline-block;
                    overflow: hidden;
                    font-size: 24px;
                    line-height: 45px;
                    letter-spacing: 7px;
                    font-weight: 700;
                    span{
                        display: inline-block;
                        opacity: 0;
                        transition: all .5s cubic-bezier(0.165, 0.84, 0.44, 1);
                    }
                }
                >span.show::before{
                    width: 100%;
                    right: 7px;
                }
                >span.show{
                    span{opacity: 1;}
                }
                >span.br{display: block !important;}
            }
            >.detail::before{
                content: "";
                position: absolute;
                right: 0; bottom: 42px;
                width: 11px; height: 11px;
                border: 1px solid #d6ad41;
                border-radius: 11px;
                opacity: .5;
            }
            >.detail{
                position: relative;
                float: right;
                padding-right: 27px;
                margin: 30% -40px;
                opacity: 0;
                transform: translateX(50px);
                transition: all .5s cubic-bezier(0.165, 0.84, 0.44, 1);
                h4{
                    position: absolute;
                    right: 0; top: 20px;
                    font-size: 11px;
                    color: #d6ad41;
                    font-weight: 700;
                    letter-spacing: 4px;
                    transform: translateX(90%) rotate(90deg);
                    transform-origin: 0%;
                }
                p{
                    width: 818px; height: 483px;
                    padding: 6px 18px 28px 16px;
                    background: url(../../img/detail/frame_flow.png) no-repeat 0 0;
                    span{
                        display: block;
                        overflow-x: hidden; overflow-y: auto;
                        width: 784px; height: 449px;
                        img{width:100%;}
                    }
                    span::-webkit-scrollbar{
                        width: 6px;
                        background-color: #F5F5F5;
                    }
                    span::-webkit-scrollbar-thumb{
                        border-radius: 6px;
                        box-shadow: inset 0 0 6px rgb(0 0 0 / 30%);
                        background-color: #888;
                    }
                    span::-webkit-scrollbar-track{
                        box-shadow: inset 0 0 6px rgb(0 0 0 / 30%);
                        border-radius: 6px;
                        background: #F5F5F5;
                    }
                }
            }
            >.detail.show{
                opacity: 1;
                transform: translateX(0);
            }
            >.detail::after{
                content: "";
                position: absolute;
                right: 5px; top: 105px; bottom: 95px;
                border-left: 1px solid #d6ad41;
                opacity: .5;
            }
        }

        .contactUp{
            color: #000;
            background-color: #fff;
            text-align: center;
            position: relative;
            display: block;
            margin: 11% 0 0 0;
            .contactCon{
                h1{
                    font-size: 9rem;
                    font-weight: 900;
                    line-height: 1.1;
                    margin: 10px auto 60px;
                    color: #2c3d45 !important;
                }
                .small{
                    text-transform: uppercase;
                    font-weight: 900;
                    font-size: 3.6rem;
                    margin: -145px auto 30px;
                    color: #d6ad41;
                }
                .layoutWrap{
                    position: relative;
                    .layout{
                        .layoutLow{
                            flex-direction: row;
                            flex-wrap: wrap;
                            min-height: 100%; max-width: 100%;
                            flex: 1 1 100%;
                            display: flex;
                            .layoutL::before{
                                content: '';
                                display: block;
                                position: absolute;
                                top: 0; bottom: 0;
                                left: 0; right: 0;
                            }
                            .layoutL{
                                float: left;
                                position: absolute; 
                                left: 27%; top: 25%;
                                width: 25%;
                                text-align: left;
                                .leftTxt{
                                    padding: 30px;
                                    h2{
                                        text-transform: uppercase;
                                        font-weight: 700;
                                        font-size: 2rem;
                                        margin: 0 auto 0 0;
                                        line-height: 1.1;
                                        color: #d6ad41
                                    }
                                    p{
                                        margin: 10px auto 0 0;
                                    }
                                }
                                .click{
                                    color : #2c3d45;
                                    font-weight: bold;
                                    margin-right: 40px;
                                    text-align: right;
                                    font-size: 18px;
                                }
                            }
                            .layoutR{
                                float: right;
                                position: absolute; 
                                right: 14%; top: 25%;
                                width: 30%;
                                text-align: left;
                                .eMail{
                                    margin: 20px 0 0 30px;
                                    display: flex;
                                    .eImg{
                                        width: 50px; height: 50px;
                                        margin-right: 30px;
                                        img{width: 100%;}
                                    }
                                    .txt{
                                        h4{
                                            font-size: 1.2rem;
                                        }
                                        p{}
                                    }
                                }
                                .git{
                                    margin: 40px 0 0 30px;
                                    display: flex;
                                    .gImg{
                                        width: 50px; height: 50px;
                                        margin-right: 30px;
                                        img{width: 100%;}
                                    }
                                    .txt{
                                        h4{
                                            font-size: 1.2rem;
                                        }
                                        p{}
                                    }
                                }
                                .phone{
                                    margin: 40px 0 0 30px;
                                    display: flex;
                                    .pImg{
                                        width: 50px; height: 50px;
                                        margin-right: 30px;
                                        img{width: 100%;}
                                    }
                                    .txt{
                                        h4{
                                            font-size: 1.2rem;
                                        }
                                        p{}
                                    }
                                }
                                .kakao{
                                    margin: 40px 0 0 30px;
                                    display: flex;
                                    .kImg{
                                        width: 50px; height: 50px;
                                        margin-right: 30px; 
                                        img{width: 100%;}
                                    }
                                    .txt{
                                        h4{
                                            font-size: 1.2rem;
                                        }
                                        p{}
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .contactDawn{
            position: relative;
            .dawn{
                position: absolute;
                left: 0; top: 489px;
                padding: 18px 0;
                width: 100%;
                background-color: #2c3d45;
                p{
                    font-size: 14px;
                    color: #fff;
                    text-align: center;
                }
            }
        }
    }



    .content.show{
        .introCon{
            position: absolute;
            left: 60%; top: 35%;
            transform: translateX(-100%);
            .inTxt{
                span::before{width: 100%;}
                span{
                    opacity: 1;
                    transform: translateY(0);
                    strong::after{
                        content: "";
                        position: absolute;
                        left: 280px; top: 0px;
                        border: 7px solid #d6ad41;
                        border-radius: 7px;
                        opacity: 1;
                        transition: .1s;
                        transition-delay: 1.5s;
                    }
                }
                
            }
            .inCon{
                transform: translateY(0px);
            }
        }
        
        .introPho{
            position: absolute;
            right: 4%; top: 0;
            width: 36%; height: 100%;
            transform: translateX(0px);
            img{width: 100%;}
        }

        .insk{
            float: left;
            position: absolute; 
            left: 25%; top: 25%;
            width: 45%;
            .infoCon{
                .con{
                    span::before{width: 100%;}
                    span{
                        opacity: 1;
                        transform: translateY(0);
                    }
                }
            }
            .skillCon{
                .conWrap{
                    overflow: hidden;
                    position: relative;
                    height: 100vh;
                    max-width: 1440px;
                    margin: 0 auto;
                    transition: .5s;
                    padding-right: 200px;
                    .tit::before{width: 100%;}
                    .tit{
                        opacity: 1;
                        transform: translateY(0);
                    }
                }
            }
        }
        .introPho2{
            float: right;
            position: absolute;
            right: 0%; top: 0;
            width: 36%; height: 100%;
            transform: translateX(0px);
            img{width: 100%;}
        }

        .wrap{
            position: relative;
            max-width: 1200px;
            margin: 0 auto;
            background: url(../../img/detail/bg_pattern4.png) no-repeat 50% -330px;
            h3.typeA{
                >span::before{
                    width: 100%;
                    right: 7px;
                }
                >span{
                    span{opacity: 1;}
                }
                >span.br{display: block !important;}
            }
            >.detail::before{
                content: "";
                position: absolute;
                right: 0; bottom: 42px;
                width: 11px; height: 11px;
                border: 1px solid #d6ad41;
                border-radius: 11px;
                opacity: .5;
            }
            >.detail{
                opacity: 1;
                transform: translateX(0);
            }
            >.detail::after{
                content: "";
                position: absolute;
                right: 5px; top: 105px; bottom: 95px;
                border-left: 1px solid #d6ad41;
                opacity: .5;
            }
        }

        .contactUp{
            .contactCon{
                .small{animation: blink .8s linear .5s;}
                @keyframes blink{
                    0%{opacity: 0;}
                    35%{opacity: 1;}
                    70%{opacity: 0;}
                    100%{opacity: 1;}
                }
                .layoutWrap{
                    position: relative;
                    .layout{
                        .layoutLow{
                            .layoutL{
                                .click{
                                    span:nth-child(1){
                                        position: absolute; right: 110px;
                                        animation: ud 3s .5s infinite;
                                    }
                                    span:nth-child(2){
                                        position: absolute; right: 70px;
                                        animation: ud 3s .8s infinite;}
                                    @keyframes ud{
                                        0%{transform: translateY(0);}
                                        30%{transform: translateY(-7px);}
                                        100%{transform: translateY(0);}
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .contactDawn{
            position: relative;
            .dawn{
                position: absolute;
                left: 0; top: 489px;
                padding: 18px 0;
                width: 100%;
                background-color: #2c3d45;
                p{
                    font-size: 14px;
                    color: #fff;
                    text-align: center;
                }
            }
        }
    }
}