        .btnTop{
            opacity: 0;
            position: fixed;
            right: 50px; bottom: 50px;
            z-index: 50;
            width: 70px; height: 70px;
            border-radius: 50%;
            background: rgba(255,255,255,0.5);
            a{
                display: block;
                width: 70px; height: 70px;
                border: 1px solid #d6ad41;
                border-radius: 50%;
                text-align: center;
                >div{
                    position: absolute;
                    left: 14%; top: 14px;
                    font-size: 14px;
                    color: #2c3d45;
                    font-weight: bold;
                    z-index: 10;
                }
                em::before{
                    content: "";
                    position: absolute;
                    left: 40%; top: 53px;
                    width: 20px; height: 3px;
                    margin-left: -12px;
                    background: #2c3d45;
                    transform-origin: 100%;
                    transform: rotateZ(50deg);
                    transition: all .3s ease-in-out;
                }
                em{
                    position: relative;
                    z-index: 2;
                    width: 68px; height: 68px;
                    overflow: hidden;
                    display: block;
                    text-indent: 100%;
                    white-space: nowrap;
                    font: 700 18px/56px 'Montserrat';
                    color: #2c3d45;
                }
                em::after{
                    content: "";
                    position: absolute;
                    left: 50%; top: 53px;
                    width: 20px; height: 3px;
                    background: #2c3d45;
                    transform-origin: 0;
                    transform: rotateZ(-50deg);
                    transition: all .3s ease-in-out;
                }
            }
        }
        .btnTop.show{
            opacity: 1;
            transition: opacity .2s ease-in-out;
        }

