            li{
                position: relative;
                float: left;
                width: 7px; height: 7px;
                margin: 0 30px;
                border-radius: 50%;
                a{
                    position: absolute;
                    left: 0; top: 0;
                    overflow: hidden;
                    display: block;
                    width: 7px; height: 7px;
                    border-radius: 50%;
                    text-indent: 100px;
                    background: #000;
                }
            }
        }
