main{
    .s1{
        position: relative;
        width: 100%; height: 100%;
        opacity: 1;
        .header{
            &.hidden{
                display: none;
            }
            position: fixed;
            left: 0; top: 0;
            z-index: 300;
            width: 100%; height: 80px;
            border-bottom: 0;
            background: none;
            transition: all .8s cubic-bezier(0.165, 0.84, 0.44, 1);
            h1{
                position: absolute;
                left: 50%; top: 30px;
                transform: translateX(-50%);
                z-index: 50;
                overflow: hidden;
                width: 50px;
                transition: all .3s cubic-bezier(0.475, 0.425, 0, 0.995);
                a{
                    img{width: 100%;}
                }
            }
            .menuBtn{
                position: absolute;
                left: 5%; top: 0;
                transition: all .3s cubic-bezier(0.475, 0.425, 0, 0.995);
                .menu{
                    position: relative;
                    z-index: 1;
                    transition: all .3s cubic-bezier(0.475, 0.425, 0, 0.995);
                    ul{
                        li{
                            float: left;
                            padding: 18px 20px;
                            margin-right:q 20px;
                            a::before{
                                content: "";
                                position: absolute;
                                left: 0; bottom: 12px;
                                z-index: -1;
                                width: 0; height: 10px;
                                background: #d6ad41;
                                transition: .3s;
                            }
                            a{
                                position: relative;
                                display: inline-block;
                                padding: 10px 2px;
                                line-height: 24px;
                                font-size: 16px;
                                color: #000;
                                font-weight: 700;
                            }
                            a.white{color: #fff;}
                        }
                        li:nth-of-type(1):hover >a::before{
                            content: ""; width: 55px;
                            transition: .3s;
                        }
                        li:nth-of-type(1).line >a::before{
                            content: ""; width: 55px;
                        }

                        li:nth-of-type(2):hover >a::before{
                            content: ""; width: 76px;
                            transition: .3s;
                        }
                        li:nth-of-type(2).line >a::before{
                            content: ""; width: 76px;
                        }

                        li:nth-of-type(3):hover >a::before{
                            content: ""; width: 36px;
                            transition: .3s;
                        }
                    }
                }
            }
        }
    }
}