    color: #000;
    vertical-align: top;
}
.list .tit .slick-slide .title a.white{color: #fff;}



/* title center */
.list .tit .slick-center .title{
    z-index: 10;
    left: 50%;
    opacity: 1;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.list .tit .slick-center .title span{
    opacity: 1;
}
.list .tit .slick-center .title span.white{
    color: #fff;
}
.list .tit .slick-center .title strong{
    display: block;
    margin: 20px 0 30px;
    font-weight: 700;
    font-size: 96px;
    line-height: 110px;
    -webkit-text-stroke-width: 2px;
    -webkit-text-fill-color: #000;
    -webkit-text-stroke-color: #000;
}
.list .tit .slick-center .title strong.white{
    -webkit-text-fill-color: #fff;
    -webkit-text-stroke-color: #fff;
}
.list .tit .slick-center .title a::before{
    content: "";
    position: absolute;
    right: 0; bottom: 1px;
    width: 6px; height: 6px;
    border-radius: 6px;
    background: #000;
    -webkit-animation: linkicon .2s linear 0s 1 normal both;
            animation: linkicon .2s linear 0s 1 normal both;
    -webkit-animation-delay: .8s;
            animation-delay: .8s;
}
@-webkit-keyframes linkicon{
	from {right:100%;}
	to {right:0;}
}
@keyframes linkicon{
	from {right:100%;}
	to {right:0;}
}
.list .tit .slick-center .title a:hover::before{content: ""; background: #d6ad41;}
.list .tit .slick-center .title a.white::before{background: #fff;}
.list .tit .slick-center .title a.white:hover::before{background: #d6ad41;}
.list .tit .slick-center .title a{opacity: 1;}
.list .tit .slick-center .title a:hover{opacity: 1; color: #d6ad41;}
.list .tit .slick-center .title a.white{color: #fff;}
.list .tit .slick-center .title a.white:hover{color: #d6ad41;}
.list .tit .slick-center .title a::after{
    content: "";
    position: absolute;
    left: 0; bottom: 0;
    width: 116px; height: 1px;
    background: #000;
    -webkit-animation: widthFull .2s linear 0s 1 normal both;
            animation: widthFull .2s linear 0s 1 normal both;
    -webkit-animation-delay: .8s;
            animation-delay: .8s;
}
@-webkit-keyframes widthFull {
	from {width:0;}
	to {width:100%;}
}
@keyframes widthFull {
	from {width:0;}
	to {width:100%;}
}
.list .tit .slick-center .title a:hover::after{content: ""; background: #d6ad41;}
.list .tit .slick-center .title a.white::after{background: #fff;}
.list .tit .slick-center .title a.white:hover::after{background: #d6ad41;}




