@charset "UTF-8";
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


* {
    box-sizing:border-box; 
    -webkit-overflow-scrolling: touch;
    font-family:"Roboto",'Noto Sans KR',sans-serif; 
    -ms-overflow-style: none; 
    -ms-user-select: none; 
    -moz-user-select: -moz-none; 
    -webkit-user-select: none; 
    -khtml-user-select: none; 
    user-select:none;    

}
::-webkit-scrollbar { display: none;}
html {height:100%; /* scroll-behavior:smooth; */}
body {height:100%; -webkit-text-size-adjust:none; }
td, div, p, li, dt, dd, input, textarea, h1, h2, h3, h4, h5 {
    font-size:16px; 
    line-height:1.6em; 
    font-weight:400; 
    color:#050505;  
    word-break:break-all;
}
html * {
	word-break:break-word;
	word-wrap:break-word;
	overflow-wrap:break-word;
	-ms-word-break :normal;
} 
header, footer, section, article, nav, aside {display:block;}
video {
    position:relative; 
    vertical-align:top; 
    z-index:10;}
textarea, .scr {
    border: none;background: none;
	overflow-x: hidden;
    overflow-y: auto; 
}

html, body	{ margin:0;padding:0;}
form, input, p, h1, h2, h3, h4, h5, h6, ul, ol, li, dl, dt, dd { margin:0;padding:0; }
ul, ol, li	{ 
    list-style:none; 
    text-decoration: none;
}
a{text-decoration: none;}
img		{ border:0;vertical-align:top}
table	{ border-collapse:collapse; border-spacing:0 !important; } 
th, td	{ padding:0;}
textarea	{ padding:0;margin:0;}
fieldset	{ display:block;border:none;padding:0;margin:0}
legend, caption	 { width:0;height:0;padding:0;margin:0;font-size:0;line-height:0;}
span.br{display: block !important;}
