/* Slider */
.slick-slider
{
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list
{
    position: relative;

    display: block;
    /* overflow: hidden; */

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
    -webkit-transform: translate3d(0, 0, 0);
       -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
         -o-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}

.slick-slide
{
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
    
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}




.list .tit .slick-slide{
    width: 100%;
    position: relative;
}
.list .tit .slick-slide .title{
    position: absolute;
    left: 50%; top: 50%;
    z-index: 9999;
    opacity: 1;
    margin: 0 30px;
    white-space: nowrap;
    color: #000;
    will-change: transform;
    transform: translate(-50%,-50%);
}
.list .tit .slick-slide .title span{
    /* opacity: 0; */
    font-size: 18px;
    font-weight: 500;
}
.list .tit .slick-slide .title span.white{color: #fff;}
.list .tit .slick-slide .title strong{
    display: block;
    margin: 20px 0 30px;
    font-weight: 700;
    font-size: 96px;
    line-height: 110px;
    -webkit-text-stroke-width: 2px;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-color: #000;
}
.list .tit .slick-slide .title strong.white{
    -webkit-text-stroke-color: #fff;
}
.list .tit .slick-slide .title a{
    /* opacity: 0; */
    position: relative;
    display: inline-block;
    padding: 0 56px 10px 0;
    font-size: 16px;
    line-height: 1em;
    font-weight: 500;
    color: #000;
    vertical-align: top;
}
.list .tit .slick-slide .title a.white{color: #fff;}



/* title center */
.list .tit .slick-center .title{
    z-index: 10;
    left: 50%;
    opacity: 1;
    transform: translate(-50%, -50%);
}
.list .tit .slick-center .title span{
    opacity: 1;
}
.list .tit .slick-center .title span.white{
    color: #fff;
}
.list .tit .slick-center .title strong{
    display: block;
    margin: 20px 0 30px;
    font-weight: 700;
    font-size: 96px;
    line-height: 110px;
    -webkit-text-stroke-width: 2px;
    -webkit-text-fill-color: #000;
    -webkit-text-stroke-color: #000;
}
.list .tit .slick-center .title strong.white{
    -webkit-text-fill-color: #fff;
    -webkit-text-stroke-color: #fff;
}
.list .tit .slick-center .title a::before{
    content: "";
    position: absolute;
    right: 0; bottom: 1px;
    width: 6px; height: 6px;
    border-radius: 6px;
    background: #000;
    animation: linkicon .2s linear 0s 1 normal both;
    animation-delay: .8s;
}
@keyframes linkicon{
	from {right:100%;}
	to {right:0;}
}
.list .tit .slick-center .title a:hover::before{content: ""; background: #d6ad41;}
.list .tit .slick-center .title a.white::before{background: #fff;}
.list .tit .slick-center .title a.white:hover::before{background: #d6ad41;}
.list .tit .slick-center .title a{opacity: 1;}
.list .tit .slick-center .title a:hover{opacity: 1; color: #d6ad41;}
.list .tit .slick-center .title a.white{color: #fff;}
.list .tit .slick-center .title a.white:hover{color: #d6ad41;}
.list .tit .slick-center .title a::after{
    content: "";
    position: absolute;
    left: 0; bottom: 0;
    width: 116px; height: 1px;
    background: #000;
    animation: widthFull .2s linear 0s 1 normal both;
    animation-delay: .8s;
}
@keyframes widthFull {
	from {width:0;}
	to {width:100%;}
}
.list .tit .slick-center .title a:hover::after{content: ""; background: #d6ad41;}
.list .tit .slick-center .title a.white::after{background: #fff;}
.list .tit .slick-center .title a.white:hover::after{background: #d6ad41;}




/* indi */
.slick-dots{
    position: absolute;
    left: 20%; top: 1600%;
    z-index: 9800;
    margin-left: -30px;
    transform: translate(50%);

}
.slick-dots li{
    position: relative;
    float: left;
    width: 5px; height: 11px;
    margin: 0 30px;
    border-radius: 50%;
}
.slick-dots li button{
    position: absolute;
    left: 0; top: 0;
    /* overflow: hidden; */
    display: block;
    width: 5px; height: 11px;
    border-radius: 50%;
    background: #000;
    border: none;
    cursor: pointer;
    text-indent: -10000px;
}
.slick-dots.white li button{
    background: #fff;
}
.slick-dots .slick-active button{
    transform: scale(1.4);
    transform-origin: center;
}